<template>
    <div class="new-product">
        <el-page-header @back="$router.push('/products')" title="back to all products" content="New product"></el-page-header>
        <h1 class="title title--primary">Launch new product</h1>
        <el-form label-position="top" :model="productForm" :rules="productFormRules" ref="productForm">
            <el-form-item label="Category" prop="category">
                <el-select v-model="productForm.category" placeholder="Select categories">
                    <el-option
                    v-for="category in categories"
                    :key="category.id"
                    :label="category.name"
                    :value="category.id">
                    </el-option>
                </el-select>
                <el-button style="margin-left: 20px;" @click="showNewCategoryForm = true">Or create a new category</el-button>
            </el-form-item>
            <el-dialog title="New category" :visible.sync="showNewCategoryForm">
                <el-form :model="newCategoryForm" ref="newCategoryForm" :rules="newCategoryFormRules">
                    <el-form-item label="Category name " style="display: flex;" prop="name">
                        <el-input v-model="newCategoryForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="Category url reference" style="display: flex;" prop="slug"> 
                        <el-input v-model="newCategoryForm.slug"></el-input>
                    </el-form-item>
                    <el-form-item label="Category description" prop="description">
                        <el-input type="textarea" v-model="newCategoryForm.description"></el-input>
                    </el-form-item>
                    <el-form-item label="Category images">
                        <el-upload
                        class="image-upload"
                        action="/api/images"
                        :on-success="loadImages"
                        multiple
                        :limit="3"
                        :on-exceed="handleExceed"
                        list-type="picture">
                            <el-button size="small" type="primary">Click to upload</el-button>
                            <div slot="tip" class="el-upload__tip">jpg/png files with a size less than 500kb</div>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="showNewCategoryForm = false">Discard</el-button>
                    <el-button type="success" @click="createCategory('newCategoryForm')">Create category</el-button>
                </span>
            </el-dialog>
            <el-form-item label="Product name" prop="name">
                <el-input v-model="productForm.name" placeholder="Choose a name" />
            </el-form-item>
            <el-form-item label="Url reference" prop="slug">
                <el-input v-model="productForm.slug" placeholder="Choose a url reference" />
            </el-form-item>
            <el-form-item label="Product description" prop="description">
                <el-input type="textarea" v-model="productForm.description" placeholder="Write a detailed description" />
            </el-form-item>
            <el-form-item label="Product images">
                <el-upload
                class="image-upload"
                drag
                action="/api/images"
                :on-success="loadImages"
                multiple
                :limit="5"
                :on-exceed="handleExceed"
                list-type="picture">
                    <el-button size="small" type="primary">Click to upload</el-button>
                    <div slot="tip" class="el-upload__tip">jpg/png files with a size less than 500kb</div>
                </el-upload>
            </el-form-item>
            <h3>Pricing and fulfillment</h3>
            <el-form-item label="Price" prop="price">
                £ <el-input-number :controls="false" v-model="productForm.price" :precision="2" />
            </el-form-item>
            <!--<el-form-item label="Height (in centimetres)" prop="height">-->
            <!--    <el-input-number :controls="false" v-model="productForm.height" :step="1" /> cm-->
            <!--</el-form-item>-->
            <!--<el-form-item label="Width (in centimetres)" prop="width">-->
            <!--    <el-input-number :controls="false" v-model="productForm.width" :step="1" /> cm-->
            <!--</el-form-item>-->
            <!--<el-form-item label="Length (in centimetres)" prop="length">-->
            <!--    <el-input-number :controls="false" v-model="productForm.length" :step="1" /> cm-->
            <!--</el-form-item>-->
            <!--<el-form-item label="Weight" prop="weight">-->
            <!--    <el-input-number :controls="false" v-model="productForm.weight" :precision="2" /> kg-->
            <!--</el-form-item>-->
            <el-alert
                v-if="errors"
                title="Whoops"
                description="Please correct the errors above"
                type="error"
                show-icon>
            </el-alert>
            <el-form-item style="margin-top: 40px;">
                <el-button type="success" @click="submitForm('productForm')">Launch product</el-button>
                <el-button type="danger">Cancel - discard product</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "new-product",
        data() {
            return {
                showNewCategoryForm: false,
                productForm: {
                    name: "",
                    description: "",
                    slug: "",
                    price: 0,
                    weight: 0,
                    height: 0,
                    length: 0,
                    width: 0,
                    category: ""
                },
                newCategoryForm: {
                    name: "",
                    slug: "",
                    description: ""
                },
                slugChanged: false,
                images: [],
                errors: false,
                categories: [],
                newCategoryFormRules: {
                    name: [
                        { required: true, message: 'Please choose a product name', trigger: 'blur' },
                        { max: 14, message: 'Category name must be less than 15 characters', trigger: 'blur' }
                    ],
                    slug: [
                        { required: true, message: 'Please choose a url reference', trigger: 'blur' },
                        { min: 3, max: 20, message: 'url reference must be between 3 and 20 characters', trigger: 'blur' },
                        { validator: this.validateSlug, message: 'Url reference cannot contain white spaces', trigger: 'blur' }
                    ],
                    description: [
                        { required: true, message: 'Please write a description', trigger: 'blur' },
                        { max: 400, message: "description cannot be more than 400 characters", trigger: 'blur' },
                    ],
                },
                productFormRules: {
                    name: [
                        { required: true, message: 'Please choose a product name', trigger: 'blur' },
                        { min: 5, max: 31, message: 'Product name must be between 5 and 30 characters', trigger: 'blur' }
                    ],
                    slug: [
                        { required: true, message: 'Please choose a url reference', trigger: 'blur' },
                        { min: 5, max: 40, message: 'url reference must be between 5 and 40 characters', trigger: 'blur' },
                        { validator: this.validateSlug, message: 'Url reference cannot contain white spaces', trigger: 'blur' }
                    ],
                    description: [
                        { required: true, message: 'Please write a description', trigger: 'blur' }
                    ],
                    price: [
                        { required: true, message: 'Please choose a product price', trigger: 'blur' },
                    ],
                    // width: [
                    //     { required: true, type: 'number', message: 'Please add dimensions in centimetres', trigger: 'blur' },
                    // ],
                    // length: [
                    //     { required: true, type: 'number', message: 'Please add dimensions in centimetres', trigger: 'blur' },
                    // ],
                    // height: [
                    //     { required: true, type: 'number', message: 'Please add height in centimetres', trigger: 'blur' },
                    //     { min: 1, message: 'Height cannot be zero', trigger: 'blur' },
                    // ],
                    // weight: [
                    //     { required: true, type: 'number', message: 'Please select activity resource', trigger: 'blur' }
                    // ],
                }
            }
        },
        mounted() {
            this.$axios.get("/api/categories")
            .then((res) => {
                this.categories = res.data;
            })
            .catch(() => {
                this.$message.error("Whoops, error loading categories. Please contact the system administrator")
            });
        },
        methods: {
            autoCreateSlug(name) {
                if(!this.slugChanged) {
                    this.slug = name.replace(/ /g, "-")
                }
            },
            handleExceed(files, fileList) {
                this.$message.warning(`The limit is 3, you selected ${files.length} files this time, add up to ${files.length + fileList.length} totally`);
            },
            createCategory(formName) {
                this.$refs[formName].validate((valid) => {
                    if(valid) {
                        this.$axios.post("/api/categories", {
                            name: this.newCategoryForm.name,
                            description: this.newCategoryForm.description,
                            slug: this.newCategoryForm.slug,
                            images: []
                        })
                        .then((res) => {
                            console.log(res)
                            this.categories.push(res.data);
                            this.showNewCategoryForm = false;
                            this.$message.success("Category created successfully. You can now select the category from the dropdown menu")
                        })
                        .catch(() => {
                            this.$message.error("Whoops, something went wrong trying to create a new category. Please try again, then contact the system administrator");
                        })
                    }
                })
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.errors = false
                        const data = this.productForm;
                        this.$axios.post("/api/products", {
                            content: {
                                name: data.name,
                                description: data.description,
                                slug: data.slug,
                            },
                            inventory: {
                                price: data.price
                            },
                            category: data.category
                        })
                        .then((res) => {
                            console.log(res)
                        })
                    } else {
                        this.errors = true;
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            loadImages(response, file, fileList) {
                console.log(response)
                console.log(file)
                console.log(fileList)
            },
            validateSlug (rule, value, callback) {
                console.log(value)
                if (value.indexOf(" ") > -1) {
                    callback(new Error('Url reference cannot contain spaces'));
                } else {
                    callback();
                }
            }
        }
    }
</script>

<style lang="scss">
    .el-alert {
        margin: 30px 0 !important;
    }
</style>