<template>
    <div class="products">
        <h1 class="title title--primary">Products</h1>
        <h3 class="title title--secondary">Filter products</h3>
        <div class="options">
            <div class="filters">
                <el-form :inline="true">
                    <el-form-item label="Category">
                        <!--<el-select v-model="categories" multiple placeholder="Select categories" @focus="getCategories()">-->
                        <el-select v-model="selectedCategories" multiple placeholder="Select categories">
                            <el-option
                            v-for="category in categories"
                            :key="category.id"
                            :label="category.name"
                            :value="category.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="Price range">
                        <el-slider
                        v-model="priceRange"
                        range
                        :step="5"
                        :max="80">
                        </el-slider>
                    </el-form-item>
                </el-form>
            </div>
            <router-link to="/products/new">
                <el-button rounded type="success">New Product</el-button>
            </router-link>
        </div>
        <ProductGrid :products="products" :loading="loading" />
    </div>
</template>

<script>
    import ProductGrid from "@/components/ProductGrid.vue"

    export default {
        name: "products",
        data() {
            return {
                selectedCategories: [],
                categories: [{
                    name: "Tote bags",
                    slug: "tote-bags",
                    description: "we make intriguing bags for intriguing people, and we make them to last",
                    images: [],
                    id: "57329852359235"
                },
                {
                    name: "Handbags",
                    slug: "handbags",
                    description: "A classic now, as they always have been and probably always will be",
                    images: [],
                    id: "264235235"
                }],
                priceRange: [0, 80],
                products: [],
                loading: true
            }
        },
        components: {
            ProductGrid
        },
        mounted() {
            this.$axios({
                method: "get",
                url: "/api/products"
            })
            .then((res) => {
                console.log(res)
                this.categories = res.data.categories;
                this.products = res.data.products;
                this.loading = false;
            })
            .catch(() => {
                this.$message.error("Error loading products. Please contact system administrator")
                this.loading = false;
            })
        },
    }
</script>

<style lang="scss" scoped>
    .options {
        display: flex;
        justify-content: space-between;
        margin: 20px 0;
        .el-form-item {
            margin-right: 40px;
        }
        .el-slider {
            width: 200px;
        }
    }
</style>